import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from '../pages/Index/template.vue'
import Detail from '../pages/Detail/template.vue'
import My from '../pages/my/template'
import Login from '../pages/Login/template'
import Register from '../pages/Register/template'
import Edit from '../pages/Edit/template'
import User from '../pages/User/template'
import Create from '../pages/Create/template'

import store from '../store'

Vue.use(VueRouter)

const routes = [
    {
        path: '/',
        name: 'Index',
        component: Index
    },
    {
        path: '/about',
        name: 'about',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
    },
    {
        path: '/detail/:blogId',
        name: 'detail',
        component: Detail
    },
    {
        path: '/my',
        name: 'my',
        component: My,
        meta: {requiresAuth: true}
    },
    {
        path: '/login',
        name: 'login',
        component: Login
    },
    {
        path: '/register',
        name: 'register',
        component: Register
    },
    {
        path: '/edit/:blogId',
        name: 'edit',
        component: Edit,
        meta: {requiresAuth: true}

    },
    {
        path: '/user/:userId',
        name: 'user',
        component: User,
        meta: {requiresAuth: true}
    },
    {
        path: '/create',
        name: 'create',
        component: Create,
        meta: {requiresAuth: true}
    }
]

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes
})

router.beforeEach((to, from, next) => {
    if (to.matched.some(record => record.meta.requiresAuth)) {
        // this route requires auth, check if logged in
        // if not, redirect to login page.
        store.dispatch('checkLogin').then(isLogin => {
            if (!store.getters.isLogin) {
                next({
                    path: '/login',
                    query: {redirect: to.fullPath}
                })
            } else {
                next()
            }
        })
    } else {
        next() // 确保一定要调用 next()
    }
})

export default router
